export default {
	data() {
		return {
			title: 'Hello',
			b: ['', '', ''],
			res_book_type: [],
			books_data: [],
			xingquretui: [],
			start: 0,
			banner: [],
			ColorList: this.ColorList
		};
	},
	onLoad() {
		this.refresh()
	},
	onPullDownRefresh(){
		this.refresh()
	},
	methods: {
		refresh() {
			var index = this;
			uni.showLoading({
				title:'正在玩命加载'
			});
			uni.request({
				url:this.serverUrl + '/banner/getBanner',
				success(res) {
					index.banner = res.data.message;
				},
				complete(res) {
					index.getBooks();
				}
			});
		},
		getBooks() {
			var index = this;
			this.start = 0;
			uni.request({
				url: this.serverUrl + '/book/getRandomBook?limit='+9,
				success: (res) => {
					for (let i = 0; i < res.data.data.length; i++) {
						index.books_data = res.data.data
					}
				},
				fail: () => {

				},
				complete: () => {
					uni.stopPullDownRefresh();
					uni.hideLoading();
					uni.hideNavigationBarLoading()
				}
			});
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		},
		swiperClick(bookId) {
			console.log(bookId);
			uni.navigateTo({
				url: '../bookDetail/bookDetail?bookId=' + (bookId+10)
			});
		}
	}
};
